<template>
	<div id='riskLib'>
		<div class="top">
			<div class="topLeft">
				<el-input class="selectInput" v-model="huahaiNumber" placeholder="昵称"></el-input>

				<el-input class="selectInput" v-model="userName" placeholder="姓名"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="收件人"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="电话号码"></el-input>

				<div class="selectInput" style="display: flex;justify-content: space-between">
					<el-dropdown split-button type="primary" @command="handleCommand" >
						{{selectCommand}}
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="待审核">待审核</el-dropdown-item>
							<el-dropdown-item command="审核通过">审核通过</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>

				<el-date-picker
				style="margin-left: -150px;"
				class="selectInput"
				v-model="startTime"
				type="daterange"
				range-separator="至"
				start-placeholder="开始日期"
				end-placeholder="结束日期">
				</el-date-picker>

				<div class="selectInput" style="display: flex;justify-content: space-between">
					<el-button type="primary" style="width:120px">查询</el-button>

					<el-button type="primary" style="width:120px" @click="addRisk">新增</el-button>
				</div>
			</div>
		</div>

		<div class="bottom">
			<el-table
			:data="tableData"
			height="1080"
			border
			stripe
			lazy
			size="mini"
			row-key="id"
			:tree-props="{children: 'children', hasChildren: 'hasChildren'}">
				<el-table-column
					class="tableClass"
					align='center'
					prop="id"
					label="头像"
					width="150">
					<template slot-scope="scope">
						<img :src="scope.row.img" alt="" style="width:50px;height:50px;border-radius: 100%;margin:5px 5px auto;">
					</template>
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="name"
					label="昵称">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount1"
					label="姓名">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount2"
					label="收件人">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="地址">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="电话">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="提交时间">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="状态">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="详情">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope.row)" type="text" size="small">详细信息</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>

		<div class="pagination">
			<pagination @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange" :total="251"></pagination>
		</div>

		<el-dialog title="风险库录入" :visible.sync="dialogFormVisible">
			<h3>收件人姓名</h3>
			<el-input>
			</el-input>

			<h3>收件人电话</h3>
			<el-input>
			</el-input>

			<h3>收件人地址</h3>
			<el-cascader
			:options="areaSelectData"
			@change="handleChange"
			class="full-width"
			size="large"
			v-model="selectedOptions"
			placeholder="请选择您所在的城市"
			/>

			<h3>详细地址</h3>
			<el-input>
			</el-input>

			<h3>企业名称</h3>
			<el-input>
			</el-input>

			<h3>信用代码</h3>
			<el-input>
			</el-input>

			<h3>法人名字</h3>
			<el-input>
			</el-input>

			<h3>收件人地址</h3>
			<el-cascader
			:options="areaSelectData"
			@change="handleChange"
			class="full-width"
			size="large"
			v-model="selectedOptions"
			placeholder="请选择您所在的城市"
			/>

			<h3>详细地址</h3>
			<el-input>
			</el-input>

			<h3>图片上传</h3>
			<el-upload
			:auto-upload='false'
			action="https://jsonplaceholder.typicode.com/posts/"
			list-type="picture-card"
			:on-preview="handlePictureCardPreview"
			:on-remove="handleRemove">
			<i class="el-icon-plus"></i>
			</el-upload>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false;selectedOptions = []">保存</el-button>
			</div>
		</el-dialog>

		<el-dialog :visible.sync="dialogVisible">
			<img width="100%" :src="dialogImageUrl" alt="">
		</el-dialog>
	</div>
</template>

<script>

import { regionData, CodeToText } from "element-china-area-data";
import pagination from "../../../components/pagination"
export default {
	name:'riskLib',
	components:{
		pagination
	},
	data(){
		return{
			dialog:'',
			huahaiNumber:'',
			userName:'',
			userId:'',
			phoneNumber:'',
			state:'',
			startTime:'',
			endTime:'',
			dialogFormVisible:false,
			dialogVisible:false,
			disabled: false,
			areaSelectData:regionData,
			selectedOptions:[],
			dialogImageUrl:'',
			fileImgList:[],
			selectCommand:'待审核',
			tableData: [
				{
					id: '12987123',
					name: '王小虎',
					amount1: '165',
					amount2: '4.43',
					amount3: 12,
					img:'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
				},
				{
					id: '12987124',
					name: '王小虎',
					amount1: '324',
					amount2: '1.9',
					amount3: 9,
					img:'https://picsum.photos/50/50'
				},
				{
					id: '12987125',
					name: '王小虎',
					amount1: '621',
					amount2: '2.2',
					amount3: 17,
					img:'https://picsum.photos/50/50'
				}
			]
		}
	},

	created(){
	},

	methods:{
		addRisk(){
			this.dialogFormVisible = !this.dialogFormVisible;
		},
		handleClick(row){
			this.dialog = row;
			this.dialogFormVisible = !this.dialogFormVisible;
			// this.$router.push("/userInfo")
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
		},

		handleChange() {
			let loc = "";
			for (let i = 0; i < this.selectedOptions.length; i++) {
				loc += CodeToText[this.selectedOptions[i]];
			}
		},

		handleCommand(command){
			this.selectCommand = command;
		},

		handleRemove(file, fileList) {
			this.fileImgList.splice(this.fileImgList.indexOf(file), 1);
			console.log(file,this.fileImgList)
		},
		handlePictureCardPreview(file) {
			this.dialogImageUrl = file.url;
			this.dialogVisible = true;
		},
		handlePreview(file){
			this.fileImgList.push(file);
			console.log(file,this.fileImgList)
		}
	}
}
</script>

<style scoped>
	#riskLib{
		box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
		padding: 20px;
		margin-top: 20px;
	}

	.top{
		height:60px;
		display: flex;
		margin-top:-20px;
		/* justify-items: space-between; */
	}

	.topLeft{
		display: flex;
		flex-wrap: wrap;
	}

	.bottom{
		overflow: auto;
	}

	.selectInput{
		width: 250px !important;
		margin-right: 30px;
		margin-top: 10px;
		height:40px
	}

	.pagination{
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 10px;
	}

</style>
